export default {
  name: 'searchGame',
  props: {
    showSearch: Boolean
  },
  data: function data() {
    return {
      search: null,
      lineList: [{
        name: '全部',
        code: null,
        selected: false
      }, {
        name: '1-4线',
        code: '1-4',
        selected: false
      }, {
        name: '5-9线',
        code: '5-9',
        selected: false
      }, {
        name: '15-25线',
        code: '15-25',
        selected: false
      }, {
        name: '30-50线',
        code: '30-50',
        selected: false
      }, {
        name: '51-243线',
        code: '51-243',
        selected: false
      }],
      providerList: [{
        name: '全部',
        code: null,
        selected: false
      }, {
        name: 'PT',
        code: 'PT',
        selected: false
      }, {
        name: 'MG',
        code: 'MG',
        selected: false
      }, {
        name: 'AG',
        code: 'AG',
        selected: false
      }, {
        name: 'TTG',
        code: 'TTG',
        selected: false
      }, {
        name: 'PP',
        code: 'PP',
        selected: false
      }, {
        name: 'PNG',
        code: 'PNG',
        selected: false
      }],
      recordList: []
    };
  },
  created: function created() {
    var recordList = localStorage.getItem('gameSearchRecordList') || '[]';
    this.recordList = JSON.parse(recordList);
  },
  destroyed: function destroyed() {},
  methods: {
    selectedItem: function selectedItem(item, list) {
      if (item.code === null) {
        this[list].forEach(function (item) {
          if (item.code !== null) item.selected = false;
        });
      } else {
        this[list][0].selected = false;
      }

      item.selected = !item.selected;
    },
    submit: function submit() {
      var lineList = this.lineList.filter(function (item) {
        return item.selected;
      }).map(function (item) {
        return item.code;
      });
      var providerList = this.providerList.filter(function (item) {
        return item.selected;
      }).map(function (item) {
        return item.code;
      });
      if (this.lineList[0].selected) lineList = null;
      if (this.providerList[0].selected) providerList = null;
      this.$emit('update:showSearch', false);
      this.$emit('filterGames', {
        lineList: lineList,
        providerList: providerList,
        search: this.search
      });

      if (this.search && this.search !== this.recordList[0]) {
        this.recordList.unshift(this.search);
      }

      localStorage.setItem('gameSearchRecordList', JSON.stringify(this.recordList.slice(0, 3)));
    }
  }
};